import axios from 'axios'
import NoProject from './noProject'
export default {
  name: 'list',
  components: {
    NoProject
  },
  data () {
    return {
      projectList: []
    }
  },
  methods: {
    getAllProject () {
      axios.get(this.url + '/project/all')
        .then(this.getProjectSucc)
    },
    getLeaderProject () {
      axios.get(this.url + '/project/leader/' + localStorage.getItem('id'))
        .then(this.getProjectSucc)
    },
    getParticipantProject () {
      axios.get(this.url + '/project/participant/' + localStorage.getItem('id'))
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.projectList = this.projectList.concat(res)
      }
    }
  },
  mounted () {
    let level = parseInt(localStorage.getItem('level'))
    if (level >= 5 || level === 2) this.getAllProject()
    else if (level === 4) {
      this.getLeaderProject()
      this.getParticipantProject()
    } else this.getParticipantProject()
  }
}



